				<el-form-item :style='{"width":"50%","margin":"0 0 30px 0","fontSize":"inherit","color":"inherit"}' v-else class="input" label="学生账号" prop="xueshengzhanghao">
					<el-input v-model="ruleForm.xueshengzhanghao" placeholder="学生账号" readonly></el-input>
